<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid p-1">
        <p class="fs-5 my-2 text-start">
          {{ this.$route.meta.title }}
        </p>
        <div v-if="!orderDetails">
          <i class="fas fa-spinner fa-spin fs-1 mt-3"></i>
        </div>
        <div v-else class="row g-0">
          <table
            class="table table-responsive table-striped table-hover align-middle"
          >
            <thead>
              <tr>
                <th>محصول</th>
                <th>کد</th>
                <th>تعداد</th>
                <th>قیمت واحد</th>
              </tr>
            </thead>
            <tbody>
              <template v-for="item in orderDetails" :key="item.id">
                <template v-if="item.all_videos_buy">
                  <tr>
                    <td>
                      <img
                        style="width: 50px"
                        :src="
                          API_STORAGE_URL + item.product.main_image_thumb_path
                        "
                      />
                      {{ item.product.name }} - دوره کامل
                    </td>
                    <td>
                      {{ item.product.id }}
                    </td>
                    <td>
                      {{ item.number }}
                    </td>
                    <td>
                      {{ this.utils.monize(item.price) }}
                      تومان
                    </td>
                  </tr>
                </template>
                <template v-else>
                  <template
                    v-for="itemDetails in item.productDetails"
                    :key="itemDetails.productDetailVideo.id"
                  >
                    <tr>
                      <td>
                        <img
                          style="width: 50px"
                          :src="
                            API_STORAGE_URL + item.product.main_image_thumb_path
                          "
                          @error="noPic"
                        />
                        جلسه
                        {{ itemDetails.numName }}
                        -
                        {{ item.product.name }}
                      </td>
                      <td>
                        {{ itemDetails.productDetailVideo.id }}
                      </td>
                      <td>1</td>
                      <td>
                        {{ this.utils.monize(itemDetails.price) }}
                        تومان
                      </td>
                    </tr>
                  </template>
                </template>
              </template>
              <tr>
                <td colspan="3"></td>
                <td>
                  تخفیف:
                  {{ this.utils.monize(totalDiscount) }}
                  تومان
                </td>
              </tr>
              <tr>
                <td colspan="3"></td>
                <td>
                  جمع:
                  {{ this.utils.monize(order.amount) }}
                  تومان
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";
import jmoment from "moment-jalaali";

export default {
  name: "OrderDetails",
  created() {
    this.arefApi = new ArefApi();
    this.utils = new utils();
    this.jmoment = jmoment;
    this.orderId = this.$route.params.orderId;
    this.getOrderDetails();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      order: null,
      orderDetails: null,
      API_STORAGE_URL: process.env.VUE_APP_API_STORAGE_URL,
    };
  },
  methods: {
    getOrderDetails() {
      this.arefApi
        .getOrderDetails(this.orderId)
        .then((resp) => {
          this.order = resp.data;
          this.orderDetails = resp.data.orderDetail;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    noPic(e){
      e.target.src = process.env.VUE_APP_API_STORAGE_URL + process.env.VUE_APP_NO_IMAGE_URL
    }
  },
  computed: {
    totalDiscount: function () {
      if (!this.order || !this.order.orderDetail) {
        return 0;
      }
      return this.order.orderDetail.reduce(function (totalDiscount, item) {
        return (
          totalDiscount + (item.total_price - item.total_price_with_coupon)
        );
      }, 0);
    },
  },
};
</script>

